import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
// material
import { styled } from '@mui/material/styles';
import { Box, Divider, Typography, Link } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
import { getMail } from '../../../redux/slices/mail';
//
import Markdown from '../../Markdown';
import Scrollbar from '../../Scrollbar';
import MailDetailsToolbar from './MailDetailsToolbar';
import MailDetailsReplyInput from './MailDetailsReplyInput';
import MailDetailsAttachments from './MailDetailsAttachments';
import { fDateTimeSuffix } from '../../../utils/formatTime';
import axios from 'axios'

// ----------------------------------------------------------------------

const RootStyle = styled('div')({
  flexGrow: 1,
  display: 'flex',
  flexDirection: 'column'
});

const MarkdownWrapperStyle = styled('div')(({ theme }) => ({
  '& > p': {
    ...theme.typography.body1,
    marginBottom: theme.spacing(2)
  }
}));

// ----------------------------------------------------------------------

export default function MailDetails() {
  const [reply, setReply] = useState([])
  const { mailId } = useParams();
  const dispatch = useDispatch();
  const mail = useSelector((state) => state.mail.mails.byId[mailId]);
  const isAttached = mail && mail.files.length > 0;

  useEffect(() => {
    dispatch(getMail(mailId));
    axios.post(`${process.env.REACT_APP_SERVER_URL}/getRepliedEmailById`, { id: mailId })
      .then((res) => {
        setReply(res.data.result)
      })
  }, [dispatch, mailId]);

  if (!mail) {
    return null;
  }

  return (
    <RootStyle>
      {mail !== undefined && mail && (
        <>
          <MailDetailsToolbar mail={mail} />

          <Divider />

          <Scrollbar sx={{ flexGrow: 1 }}>
            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography variant="h3" gutterBottom>
                {mail.subject}
              </Typography>
              <MarkdownWrapperStyle>
                <Markdown children={mail.message} />
              </MarkdownWrapperStyle>
            </Box>
          </Scrollbar>

          {isAttached && <MailDetailsAttachments mail={mail} />}

          <Divider />

          {reply && reply.map((value) => (
            // <Scrollbar key={value} sx={{ flexGrow: 1 }}>
              <Box key={value.id} sx={{ px: { xs: 3, md: 5 }, py: 2 }}>
                <MarkdownWrapperStyle>
                  <Markdown children={value.message} />
                </MarkdownWrapperStyle>

                <Box sx={{ display: 'flex' }}>
                  <Typography variant="caption" sx={{ color: 'text.secondary', display: 'block', flex: '1 1 0%' }}>
                    <Link color="inherit" key={value.accept}>
                      {value.accept}
                    </Link>
                  </Typography>
                  <Typography variant="caption" sx={{ color: 'text.secondary', flex: '1 1 0%', textAlign: 'right' }}>
                    {fDateTimeSuffix(value.createdAt)}
                  </Typography>
                </Box>
              </Box>
            // </Scrollbar>
          ))}

          <Divider />

          <MailDetailsReplyInput emailID={mailId} mail={mail} />
        </>
      )}
    </RootStyle>
  );
}
